<template>
    <div>
        <enhanced-input type="checkbox" :id="id" :name="name" :value="value" @input="inputChange()" :disabled="disabled" v-model="inputModel" :label="label" :subClass="subClass" :animate="animate" />
        // in check {{ inputModel }}
    </div>

</template>

<script>

    import EnhancedInput from './EnhancedInput.vue'

    export default {
      components: {EnhancedInput},
      model: {
        prop: 'checked'
      },
      props: {
        label: {
          type: String,
          required: true
        },
        id: {
          type: String,
          default: 'enhancedCheck'
        },
        checked: {
          default: false
        },
        name: {
          default: ''
        },
        value: {
          default: ''
        },
        subClass: {
          type: String,
          default: 'default'
        },
        disabled: {
          type: Boolean,
          default: false
        },
        animate: {
          type: Boolean,
          default: false
        }
      },
      data () {
        return {
          inputModel: this.checked
        }
      },
      methods: {
        inputChange () {
          this.$emit('input', this.inputModel)
        }
      }
    }
</script>

<style>
</style>



