<template>
    <div class="enhancedCheck" :class="computedClass">
        <div v-for="inputElmt in inputList">

            <enhanced-input type="checkbox" :id="inputElmt.id" :name="inputElmt.name" :value="inputElmt.value" @input="inputChange(inputElmt)" :disabled="inputElmt.disabled" v-model="inputElmt.checked" :label="inputElmt.label" :subClass="subClass" :animate="animate" :combine="combine" />
            // in group elmt {{ inputElmt.checked }}

        </div>

        // here i have {{ inputModel }}


    </div>
</template>

<script>

    import EnhancedInput from './EnhancedInput.vue'

    export default {
      components: {EnhancedInput},
      model: {
        prop: 'groupModel'
      },
      props: {
        label: {
          type: Array,
          required: true
        },
        value: {
          type: Array,
          default: () => []
        },
        id: {
          default: 'enhancedCheckGroup'
        },
        name: {
          default: ''
        },
        groupModel: {
          default: () => []
        },
        subClass: {
          type: String,
          default: 'default'
        },
        disabled: {
          default: false
        },
        combine: {
          type: Boolean,
          default: false
        },
        inline: {
          type: Boolean,
          default: false
        },
        animate: {
          type: Boolean,
          default: false
        }
      },
      data () {
        return {
          inputModel: this.groupModel
        }
      },
      mounted () {
        if (this.value.length === 0) this.value = this.label
      },
      computed: {
        inputList () {
          let list = []
          for (let i = 0; i < this.label.length; i++) {
            let idElmt = 0
            if (Array.isArray(this.id)) {
              idElmt = this.id[i]
            } else {
              idElmt = this.id + '_' + i
            }
            console.log('build', this.inputModel, this.valueList[i], this.inputModel.indexOf(this.valueList[i]) >= 0)
            const elmt = {
              id: idElmt,
              label: this.label[i],
              name: this.nameList[i],
              value: this.valueList[i],
              disabled: this.disabledList[i],
              checked: true
            }
            list.push(elmt)
          }
          return list
        },
        nameList () {
          return this.generateListFromProp(this.name)
        },
        valueList () {
          return this.generateListFromProp(this.value)
        },
        disabledList () {
          return this.generateListFromProp(this.disabled)
        },
        computedClass () {
          let computedClass = 'enhancedCheck-' + this.subClass
          if (this.combine) computedClass += ' enhancedCheck-combine'
          if (this.inline) computedClass += ' enhancedCheck-inline'
          if (this.animate) computedClass += ' enhancedCheck-animate'
          return computedClass
        }
      },
      methods: {
        generateListFromProp (propValue) {
          if (!Array.isArray(propValue)) {
            const elmtCount = this.id.length
            if (elmtCount === 1) return [propValue]
            return new Array(elmtCount).fill(propValue)
          }
          return propValue
        },
        inputChange (elmt) {
          if (elmt.checked) {
            this.inputModel.push(elmt.value)
          } else {
            const index = this.inputModel.indexOf(elmt.value)
            if (index >= 0) this.inputModel.splice(index, 1)
          }
          console.log('received ', this.inputModel, elmt)
          this.$emit('input', this.inputModel)
        }
      }
    }
</script>

<style>
</style>



