<template>
    <div class="enhancedCheck" :class="computedClass">
        <input type="radio" :id="id" :name="name" :value="value" @change="inputChange()" :disabled="disabled" v-model="checkModel" v-if="type === 'radio'">
        <input type="checkbox" :id="id" :name="name" :value="value" @change="inputChange()" :disabled="disabled" v-model="checkModel" v-else>
        <label :for="id">{{ label }}</label>
        // unit input {{ checkModel }}
    </div>
</template>

<script>

    export default {
      model: {
        prop: 'inputModel'
      },
      props: {
        type: {
          type: String,
          default: 'checkbox'
        },
        label: {
          type: String,
          required: true
        },
        id: {
          type: String,
          default: 'enhancedCheck'
        },
        inputModel: {
          default: false
        },
        name: {
          default: ''
        },
        value: {
          default: ''
        },
        subClass: {
          type: String,
          default: 'default'
        },
        disabled: {
          type: Boolean,
          default: false
        },
        animate: {
          type: Boolean,
          default: false
        }
      },
      data () {
        return {
          checkModel: this.inputModel
        }
      },
      computed: {
        computedClass () {
          let computedClass = 'enhancedCheck-' + this.subClass
          if (this.animate) computedClass += ' enhancedCheck-animate'
          return computedClass
        }
      },
      methods: {
        inputChange () {
          this.$emit('input', this.checkModel)
        }
      }
    }
</script>

<style>
</style>



